:root {
  --dark: rgb(0, 0, 0);
  --dark10: #2e2e2e;
  --light: white;
  --light10: #808080;
}

body {
  max-width: 100%;
  overflow-x: hidden;
  min-width: 400px;
  margin: 0;
  background-color: var(--dark);
  color: var(--light);
  font-size: 18px;
  font-family: "Courier New", Courier, monospace;
  height: 1000px;
  margin: auto;
  padding: 0;
  align-items: center;
}

.leftBar {
  text-align: right;
  text-decoration: underline;
  width: 160px;
  margin-left: 10px;
  position: fixed;
  margin-top: -145px;
}

.subs {
  margin-bottom: 10px;
}

.subs button {
  cursor: pointer;
  background: transparent;
  border: none;
  color: var(--light);
  text-decoration: underline;
  font-size: 18px;
  font-family: "Courier New", Courier, monospace;
  margin: 0;
  padding: 0;
}

.page {
  margin: auto;
  padding-top: 130px;
  width: 500px;
  align-items: center;
  height: auto;
  overflow: auto;
  padding-bottom: 60px;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: c(--dark);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: var(--dark10);
  border-radius: 10px;
}

.header {
  background-color: var(--dark);
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: flex-end;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  padding: 10px;
  margin-left: 135px;
}

.objHolder {
  margin-left: 190px;
}

.bottom {
  margin-top: 0px;
}

.subs :disabled {
  text-decoration: none;
  cursor: default;
}

.avatar img {
  width: 130px;
  height: 130px;
}

.shake {
  animation: shake 0.2s;
  animation-iteration-count: infinite;
}

.avatar {
  margin-bottom: 10px;
}
@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}
.profile div {
  margin-top: 10px;
}

.profile .links a {
  margin-right: 20px;
}

a {
  text-decoration: underline;
  cursor: pointer;
  color: var(--light);
}

a:hover {
  text-decoration: none;
}

.description {
  color: var(--light10);
}

button:hover {
  text-decoration: none;
}

.tokenThumbnail {
  margin-bottom: 10px;
}

.tokenThumbnail img {
  width: 288px;
}
